.page-footer {
    background-color: $secondary-bg-color;
    &__title {
        font-family: $secondary-font;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 1.16;
        text-transform: uppercase;
        color: $label-color;
        width: 100%;
        max-width: 232px;
    }
    &__span-title {
        display: block;
        font-size: 34px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        color: $accent-color;
        margin-top: 17px;
        margin-bottom: 43px;
    }

    &__social.list {
        display: flex;
        margin-bottom: 46px;
    }

    &__social-link:hover &__social-icon,
    &__social-link:focus &__social-icon {
        fill: $accent-color;
    }

    &__social-icon {
        width: 34px;
        height: 34px;
        fill: $label-color;
        transition: fill 300ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    &__social-item:not(:last-child) {
        margin-right: 28px;
    }

    &__auth-link {
        font-family: $main-font;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 1.86;
        color: $primary-desc-color;
        position: relative;
    }
    &__span-tell {
        color: $accent-color;
        margin-left: 5px;
    }
    &__social-wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        padding-bottom: 43px;
    }

    &__license {
        font-family: $main-font;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.52;
        color: #907E82;
        max-width: 145px;

    }
    &__license-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 25px;
        padding-bottom: 25px;
        border-top: 1px solid #E1E1E1;
        width: 100%;
    }

    &__auth-link::before {
        content: "";
        display: block;
        background-image: url('../images/tel.png');
        background-size: contain;
        width: 25px;
        height: 25px;
        position: absolute;
        left: -35px;
        animation: 2000ms ease-in-out 0s normal none infinite running trambling-animation;
    }

    @keyframes trambling-animation {
        0%,
        50%,
        100% {
            transform: rotate(0deg);
        }
        10%,
        30% {
            transform: rotate(-10deg);
        }
        20%,
        40% {
            transform: rotate(10deg);
        }
    }

@media screen and (min-width: 768px) {
    &__title {
        font-size: 26px;
        max-width: 340px;
    }

    &__span-title {
        font-size: 48px;
        margin-bottom: 0;
    }

    &__auth-link {
        font-size: 14px;
    }
    &__social-item:not(:last-child) {
        margin-right: 30px;
    }

    & .container {
        display: flex;
        justify-content: space-between;
        padding-bottom: 91px;
    }
    &__license {
      line-height: 1.86;
      max-width: 280px;
    }
    &__license-wrapper {
      padding-top: 29px;
      padding-bottom: 18px;
      border-top: 1px solid #E1E1E1;
      width: 100%;
    }
    &__social-wrapper {
      padding-bottom: 0;
    }
}
@media screen and (min-width: 1280px) {
  &__title {
    font-size: 30px;
    max-width: 475px;
  }

  &__span-title {
    font-size: 58px;
    margin-bottom: 0;
  }

  &__auth-link {
    font-size: 16px;
  }

  &__license-wrapper {
    padding-top: 10px;
    padding-bottom: 10px;
  }
    &__social-item:not(:last-child) {
      margin-right: 25px;
    }
}
}
